<template>
  <div class="panel">
    <div class="log-logo">登录成功!</div>
    <a href="javascript:;" class="log-btn" @click="logout">登出</a>
    <Loading v-if="isLogout" marginTop="-30%"></Loading>
  </div>
</template>

<script>
import Loading from "@/components/Loading.vue";

export default {
  name: "User",
  data() {
    return {
      isLogout: false,
    };
  },
  components: {
    Loading,
  },
  methods: {
    //登出逻辑
    logout() {
      this.isLogout = true;

      setTimeout(() => {
        window.delCookie("session");
        this.$router.push("./login");
      }, 1000);
    },
  },
};
</script>

